.p-gnav {
  /*position:fixed;にし、z-indexの数値を大きくして前面へ*/
  position: fixed;
  z-index: 999;
  /*ナビのスタート位置と形状*/
  top: 0;
  right: -120%;
  width: 40%;
  height: 100vh;
  /*ナビの高さ*/
  background: rgba(255, 255, 255, 0.6);
  /*動き*/
  transition: all 0.6s;
}

/*アクティブクラスがついたら位置を0に*/
.p-gnav.js-panelactive {
  right: 0;
}

/*ナビゲーションの縦スクロール*/
.p-gnav.js-panelactive .p-gnav__list {
  /*ナビの数が増えた場合縦スクロール*/
  position: fixed;
  z-index: 999;
  width: 40%;
  height: 100vh;
  /*表示する高さ*/
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

/*ナビゲーション*/
.p-gnav ul {
  /*ナビゲーション天地中央揃え*/
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}

/*リストのレイアウト設定*/
.p-gnav li {
  list-style: none;
  text-align: center;
  display: block;
  width: 100%;
}

.p-gnav li a {
  display: block;
  width: 100%;
  color: #111;
  text-decoration: none;
  padding: 0.625rem;
  display: block;
  text-transform: uppercase;
  letter-spacing: 0.075rem;
  font-weight: bold;
  font-family: "Poppins", sans-serif;
}

