.c-humburger {
  /*ボタン内側の基点となるためrelativeを指定。
  追従するナビゲーションの場合はfixed＋top、rightといった位置をセットで指定*/
  position: fixed;
  top: 0.9375rem;
  right: 0.9375rem;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 5px;
  z-index: 9999;
}
@media screen and (min-width: 768px) {
  .c-humburger {
    top: 1.9375rem;
    right: 10rem;
  }
}

/*ボタン内側*/
.c-humburger span {
  display: inline-block;
  transition: all 0.4s;
  /*アニメーションの設定*/
  position: absolute;
  left: 14px;
  height: 3px;
  border-radius: 2px;
  background: #fff;
  width: 45%;
}
@media screen and (min-width: 768px) {
  .c-humburger span {
    width: 70%;
  }
}

.c-humburger span:nth-of-type(1) {
  top: 15px;
}

.c-humburger span:nth-of-type(2) {
  top: 23px;
}

.c-humburger span:nth-of-type(3) {
  top: 31px;
}

/*activeクラスが付与されると
線と周りのエリアが回転して×になる*/
.c-humburger.js-active .c-humburger__area {
  transform: rotateY(-360deg);
}

.c-humburger.js-active span:nth-of-type(1) {
  top: 18px;
  left: 18px;
  transform: translateY(6px) rotate(-135deg);
  width: 30%;
}

.c-humburger.js-active span:nth-of-type(2) {
  opacity: 0;
}

.c-humburger.js-active span:nth-of-type(3) {
  top: 30px;
  left: 18px;
  transform: translateY(-6px) rotate(135deg);
  width: 30%;
}

